.App {
	height: 100vh;
	width: 100vw;
	position: absolute;
	top: 0;
	right: 0;
	display: flex;
	align-items: stretch;
}

.Main {
	position: relative;
	display: flex;
	flex-direction: column;
}

.SidebarAnimation {
	position: relative;
	overflow: visible;
	width: 232px;
	will-change: transform;
}

.OpenSidebar {
	position: absolute;
	top: 10px;
	left: -20px;

	background: transparent;
	border: none;
	cursor: pointer;
	padding: 0;
	border-radius: 20px;
	overflow: hidden;

	box-shadow: 0 4px 4px 0 #00000040;
	circle {
		box-shadow: 0 8px 10px 0 #0000000f;
		transition: fill 0.25s;
	}

	transition: transform 0.25s, box-shadow 0.25s;
	&:hover {
		transform: scale(1.08);
		box-shadow: 0 6px 5px 0 #00000030;
		circle {
			fill: #f0f6ff;
		}
	}
}
